@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&family=Nunito+Sans:wght@300&display=swap');

.post {
    display: inline-block;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 12px;
    margin: 5px;
    /* border-radius: 7px; */
    background-color: rgb(50, 50, 60);
    transition: ease-in-out 0.5s;
}

.post:hover .post-header {
    background-color: rgba(0, 0, 10, 0.9);
    transition: ease-in-out 0.5s;
}

.post-envelope {
    position: relative;
    height: 400px;
    min-width: 200px;
}

.post-header {
    position: absolute;
    width: 100%;
    background-color: rgba(0, 0, 10, 0.7);
    text-shadow: 0 0 5px black;
    z-index: 1;
    transition: ease-in-out 0.5s;
}

.post-title {
    float: left;
    padding: 7px;
}

.post-date {
    float: right;
    padding: 7px;
}

.post-image {
    position: relative;
    margin-bottom: 0;
    height: 400px;
    z-index: 0;
}
